.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: rgb(33,62,99);
    background: linear-gradient(0deg, #213e63 0%, #2f92f2 67%, #87bdf7 100%);
}





.containerHome{
    width: 100%;
    height: 100%;
    scroll-snap-type: y mandatory;

}

.sectionHome {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;    
    scroll-snap-align: start;
}

.titleHome{
    font-size: 1.25rem;
    /*color: #2b7ccd;*/
    color: #87bdf7;
} 

.NameHome{
    font-size: 1.25rem;
}

.containerInfoName{
    display: flex;
    flex-direction: column;
    width: 50%;
    text-align: center;
}

.HomeStyle{
    display: flex;
    flex-direction: row;
    /*color: #2e3842;*/
    /*background:#c8ddf2;*/
    color: white;
    background: rgb(33,62,99);
    background: linear-gradient(0deg, #213e63 0%, #2f92f2 67%, #87bdf7 100%);
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    flex-wrap: wrap;
    -webkit-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -ms-user-select:none;
    margin-top: -50px;
}

.profilePictureStyle{
    border-radius: 10px 0px 0px 10px;
    width: 50%;
    z-index: 1;
}



@media screen and (max-width: 1300px) {
    .NameHome{
        font-size: 1rem;
    }
    .titleHome{
        font-size: 1rem;
    }
    .profilePictureStyle{
        border-radius: 10px 0px 0px 10px;
    }
    .centradoHome{
        margin: 5px;
    }
    .HomeStyle{
        margin-top: 0px;
    }

}



