.one, .two, .three{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    scroll-snap-align: start;
    color: white;
    justify-content: space-around;
}

.containerVentana1{
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    height: 100vh;
}

.one{
    background: rgb(35,39,65);
    background: linear-gradient(0deg, rgba(35,39,65,1) 0%, rgba(106,90,205,1) 27%);
    
}

.two{
    background: rgb(35,39,65);
    background: linear-gradient(0deg, rgba(35,39,65,1) 0%, rgba(0,128,128,1) 27%);
}

.three{
    background: rgb(35,39,65);
background: linear-gradient(0deg, rgba(35,39,65,1) 0%, rgba(255,99,71,1) 27%);
}

.pineBackground{
    max-height: 50%;
}
